<template>
  <div class="bs-docs-section" id="slider">
    <h3 class="page-header"><a href="#slider" class="anchor">slider </a></h3>
    <div class="bs-example">
      <h4>basic</h4>
      <slider :value="30"></slider>
      <br><br>
      <slider :value="[20,50]"></slider>
