<script>
import {Slider, Layout, Input} from 'src/';
const VCol = Layout.Col;
const VRow = Layout.Row;
const VInput = Input;

export default{
  components: {
    Slider,
    VCol,
    VRow,
    VInput
  },

  data () {
    return {
      sliderValue: 1
    }
  },

  watch: {
    sliderValue (val) {
      console.log(''+val);
    }
  },

  events: {
    'slider:onChange' (mod, values) {
      console.log('onChange');
      console.log(values);
    },
    'slider:onAfterChange' (mod, values) {
      console.log('onAfterChange');
      console.log(values);
    }
  }
}
</script>




