<template>
  <div class="bs-docs-section" id="cascader">
    <h3 class="page-header"><a href="#cascader" class="anchor">Cascader </a></h3>
    <div class="bs-example">
      <cascader :options="options" @change="selectChange" :default-value="defaultValue"></cascader>
    </div>
    <pre><code class="language-markup"><script type="language-mark-up">
<cascader :options="options" @change="selectChange" :default-value="defaultValue"></cascader>

const options = [{
    value: 'zhejiang',
    label: '',
    children: [{
      value: 'hangzhou',
      label: '',
      children: [{
        value: 'xihu',
        label: '',
      }],
    }],
  }, {
    value: 'jiangsu',
    label: '',
    children: [{
      value: 'nanjing',
      label: '',
      children: [{
        value: 'zhonghuamen',
        label: '',
      }],
