      selectChange(selectedValue, option) {
        alert(selectedValue)
      }
    }
  }
</script></code></pre>

  <h2>Options</h2>
  <table class="atui-table table-bordered">
    <thead>
      <tr>
        <th>Name</th>
        <th>Type</th>
