  </div>
</template>

<script>
  const options = [{"value":"2016","label":"2016","children":[{"value":"01","label":"","disabled":false},{"value":"02","label":"","disabled":false},{"value":"03","label":"","disabled":false},{"value":"04","label":"","disabled":true},{"value":"05","label":"","disabled":true},{"value":"06","label":"","disabled":true},{"value":"07","label":"","disabled":true},{"value":"08","label":"","disabled":true},{"value":"09","label":"","disabled":true},{"value":"10","label":"","disabled":true},{"value":"11","label":"","disabled":true},{"value":"12","label":"","disabled":true}]},{"value":"2015","label":"2015","children":[{"value":"01","label":""},{"value":"02","label":""},{"value":"03","label":""},{"value":"04","label":""},{"value":"05","label":""},{"value":"06","label":""},{"value":"07","label":""},{"value":"08","label":""},{"value":"09","label":""},{"value":"10","label":""},{"value":"11","label":""},{"value":"12","label":""}]}]

  const defaultValue = ['2016', '05']

  import {Cascader} from 'src/'
  export default {
    components: {
      Cascader
    },
    data() {
      return {
        options:options,
        defaultValue:defaultValue
      }
    },
    methods:{
      selectChange(selectedValue, options) {
        console.log(selectedValue,options)
      }
    }
  }
</script>



