      },
      changeLoading() {
        this.loading = !this.loading
      },
      changeSize() {
        this.size = "default"==this.size ? "middle" : "middle" == this.size ? "small" : "default"
      },
      changeFixed() {
        this.fixedHeader = !this.fixedHeader
      }
    }
  }
</script>



