<script>
  import {Pagination} from 'src/'
  export default {
    data () {
      return {
        totalPage: 100,
        currPage: 10,
        showJumper: true,
        total: 100,
        showSizeChanger: true,
        defaultSize:20
      }
    },
    components: {
      Pagination
    },
    events: {
      pageChange (page, id) {

      },
      'pagination-size-change' (data) {
        console.log('EVENT', 'pagination-size-change', data.label, data.value)
      }
    },
    methods:{
      changeTotalPage() {
        this.totalPage = 50
        this.currPage = 20
        this.total = 1000
        this.defaultSize = 30
      }
    },
    ready () {

    }
  }
</script>

<style lang="less">
