<template>
  <div class="bs-docs-section" id="textarea">
    <h3 class="page-header"><a href="#textarea" class="anchor">Textarea </a></h3>
    <div class="bs-example">
    <h4></h4>
    <div>
      <v-textarea placeholder=""></v-textarea>
    </div>
    <div>
      <v-textarea disabled content=""></v-textarea>
    </div>
    <div>
      <v-textarea error content=""></v-textarea>
    </div>
    <h4></h4>
    <div>
      <v-textarea placeholder="" :words-count="true"></v-textarea>
    </div>
    <div>
      <v-textarea placeholder="" :words-count="true" count-tips=""></v-textarea>
    </div>
    <h4></h4>
    <div>
      <v-textarea :limit-words="50" placeholder="50" :error="validStatus == 'error'" name="ruohuan" v-ref:textarea-limit :content.sync="content"></v-textarea>
    </div>
    <div>
      <v-textarea disabled :limit-words="10" placeholder="10"></v-textarea>
    </div>
<pre><code class="language-markup"><script type="language-mark-up">
<v-textarea disabled :limit-words="10" placeholder="10"></v-textarea>
<v-textarea placeholder="" :words-count="true" count-tips=""></v-textarea>
</script></code></pre>
    <h4></h4>
    <table class="atui-table table-bordered">
      <thead>
        <tr>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
        </tr>
      </thead>
      <tbody>
        <tr>
          <td>content</td>
          <td><code>String</code></td>
          <td><code></code></td>
          <td></td>
        </tr>
        <tr>
          <td>placeholder</td>
          <td><code>String</code></td>
          <td><code></code></td>
