          <td>success</td>
          <td></td>
          <td></td>
          <td></td>
        </tr>
        <tr>
          <td>limit-words</td>
          <td><code>Number</code></td>
          <td></td>
          <td></td>
        </tr>
        <tr>
          <td>words-count</td>
          <td><code>Boolean || </code></td>
          <td></td>
          <td></td>
        </tr>
        <tr>
          <td>count-tips</td>
          <td><code>String</code></td>
          <td><code></code></td>
