        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
  import {Textarea as vTextarea} from 'src/'

  export default {
    components: {
      vTextarea
    },

    data () {
      return {
        validStatus: '',
        content: '2'
      }
    },

    watch: {
      content (val) {
        let len = val.length;

        if(len < 2) {
          this.validStatus = 'error';
        }
      }
    }
  }
</script>



